<template>
  <div>
    <div class="big-stats">
      <div class="wpm">
        <p class="unit">
          WPM
        </p>
        <h2 class="value">
          {{ format(WPM, 0, 1) }}
        </h2>
      </div>
      <div class="cpm">
        <p class="unit">
          CPM
        </p>
        <h2 class="value">
          {{ format(CPM, 0, 1) }}
        </h2>
      </div>
      <div class="time">
        <p class="unit">
          Time
        </p>
        <h2 class="value">
          {{ minutes ? `${minutes}:${seconds}` : `${seconds}s` }}
        </h2>
      </div>
    </div>
    <div v-if="mistakes.length" class="mistakes">
      <ul>
        <li>Time wasted by mistakes: {{ format(totalTimeLost) }} s</li>
        <li>Speed counting down that time {{ format(WPMWithoutTimeLost, 0, 1) }} WPM</li>
        <li>Most mistakes in a row: {{ mostMistakesInARow }} mistakes</li>
        <li>Longest correction time: {{ format(longestTimeOfCorrection) }} s</li>
      </ul>
    </div>
    <h2 v-else>
      You haven't made any mistakes!
    </h2>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'StatsCard',
  props: {
    stats: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      mostFrequent: [],
    };
  },
  computed: {
    ...mapGetters(['room']),
    minutes() {
      return Math.floor(this.stats.timeFromFirstInput / 1000 / 60);
    },
    seconds() {
      return Math.round((this.stats.timeFromFirstInput / 1000) % 60);
    },
    correctCharsLength() {
      return this.history.filter((change) => change.type === 'correct').length;
    },
    correctInputsLength() {
      return this.history.filter((change) => change.type === 'correct' || change.type === 'backspace').length;
    },
    CPM() {
      return this.correctCharsLength / this.format(this.stats.timeFromFirstInput, 4) * 60;
    },
    WPM() {
      return this.CPM / 5;
    },
    history() {
      return this.stats.history;
    },
    mistakes() {
      return this.history.filter((change) => change.type === 'mistake');
    },
    mistakesToClicksRatio() {
      return this.mistakes.length / (this.history.length - 1);
    },
    mostMistakesInARow() {
      return this.mistakes.map((obj) => obj.fixQueuePos)
        .reduce((acc, value) => Math.max(acc, value), 0);
    },
    correctionTimes() {
      const timesAcc = [];
      for (let i = 0; i < this.history.length; i += 1) {
        // / log(this.history[i]);
        if (this.history[i].type === 'mistake') {
          const startTime = this.history[i].time;
          // console.log(`StartTime ${startTime}; Expected '${this.history[i].expectedText}'`);
          // i+1 must be backspace or another mistake
          for (let j = i + 1; j < this.history.length; j += 1) {
            // console.log(`Looking for correction: ${this.history[j].type}`);
            if (this.history[j].type === 'correct') {
              // console.og(this.history[j].time - startTime);
              timesAcc.push(this.history[j].time - startTime);
              i = j;
              break;
            }
          }
        }
      }
      return timesAcc;
    },
    totalTimeLost() {
      return this.correctionTimes.reduce((acc, value) => acc + value, 0);
    },
    WPMWithoutTimeLost() {
      return this.correctCharsLength / this.format(this.stats.timeFromFirstInput - this.totalTimeLost, 4) * 60 / 5;
    },
    longestTimeOfCorrection() {
      return this.correctionTimes.reduce((acc, value) => Math.max(acc, value), 0);
    },
    startReactionTime() {
      return this.stats.firstCharTime - this.stats.startTime;
    },
    keyPressAvgInterval() {
      return this.stats.timeFromFirstInput / this.history.length;
    },
    correctKeyPressAvgInterval() {
      return this.stats.timeFromFirstInput / this.correctInputsLength;
    },
  },
  mounted() {
    if (this.room.connected) {
      this.$socket.client.emit('completedStats', {
        wpm: this.format(this.WPM, 0, 1),
        time: this.stats.timeFromFirstInput,
      });
    }
  },
  methods: {
    format(number, precision = 2, scaler = 0.001) {
      return Math.round(number * scaler * (10 ** precision)) / (10 ** precision);
    },
  },
};
</script>

<style lang="sass" scoped>
.big-stats
  width: 100%
  display: flex
  justify-content: space-between
  text-align: center
  margin-bottom: 1rem
  font-size: 3rem

  .unit
    font-size: .5em

li
  list-style-position: inside
  margin: 5px 0

</style>
